<template>
<div>
   <loading :active.sync="isLoading"></loading>
  <div>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col" width="200"></th>
          <th scope="col" width="110"></th>
          <th scope="col" width="80"></th>
          <th scope="col" width="130"></th>
          <th scope="col" width="130"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons" :key="item.id">
           <td>{{item.title}}</td>
          <td>{{item.code}}</td>
          <td>{{item.due_date}}</td>
          <td>
            {{item.percent}} </td>
            <td>
            <span v-if="item.is_enabled"></span>
            <span v-if="!item.is_enabled"></span>
            </td>
         
          <td>
            <a
              class="btn btn-outline-primary btn-sm"
              href="#"
              @click.prevent="openModal(false,item)"
            ></a>
            <a
              class="btn btn-outline-danger btn-sm"
              href="#"
              @click="openDelCouponModal(item)"
            ></a>
          </td>
        </tr>
      </tbody>
    </table>
   <pagination :pagination="pagination" @gopage="getCoupon"></pagination>
    
    
  </div>
 
      <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="couponModalLabel">
              <span v-if="isNew"></span>
              <span v-if="!isNew"></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                    v-model="tempCoupon.title"
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-12">
                    <label for="category"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="category"
                      placeholder=""
                      v-model="tempCoupon.percent"
                    />
                  </div>
                  <div class="form-group col-md-12">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                      v-model="tempCoupon.code"
                    />
                  </div>
                  <div class="form-group col-md-12">
                    <label for="origin_price"></label>
                    <input
                      type="date"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                      v-model="tempCoupon.due_date"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">
              <i class="fas fa-spinner fa-spin" v-if="isLoading"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="delCoupon"
              ></button>
          </div>
        </div>
      </div>
    </div>
</div>
          
        
</template>

<script>
import $ from 'jquery';
import Pagination from "../Pagination";
export default {
    data(){
      return{
        coupons:[],
        tempCoupon: {},
        isNew: false,
        isLoading:false,
         status: {
       
      },
        pagination:{},
      };
    },
    components:{
      Pagination
    },
    methods:{
      getCoupon(page = 1) {
      let vm = this;
      vm.isLoading = true;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      vm.$http.get(api).then(response => {
        console.log("", response);
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempCoupon = {};
        
        this.isNew = true;
      } 
     else {
        this.tempCoupon = Object.assign({}, item);
       
        this.isNew = false;
      }
      $("#couponModal").modal("show");
    },
    
     updateCoupon() {
      let vm = this;
      vm.isLoading = true;
     let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`; 
      let httpMethod = 'post';
      if (!vm.isNew) {
       api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
     
      const coupon = vm.tempCoupon;
      vm.$http[httpMethod](api, { data: coupon }).then(response => {
        if (response.data.success) {
          console.log("updateCoupon:", response);
          vm.isLoading = false;
          $("#couponModal").modal("hide");
          vm.getCoupon(); 
        } else {
          console.log("", response);
          vm.isLoading = false;
          $("#couponModal").modal("hide");
        }
      });
    },
     openDelCouponModal(item){
      this.tempCoupon = item; //tempProduct
      console.log(this.tempCoupon)
      $("#delCouponModal").modal("show");//
    },
    delCoupon(){
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      this.$http.delete(api).then(response => {
      
          vm.coupons = response.data.coupons;
        if(response.data.success){
          $("#delCouponModal").modal("hide");
          vm.getCoupon(); 
          console.log('');
        }else{
          $("#delCouponModal").modal("hide");
          vm.getCoupon();
          console.log('');
        }
      });
    }, 
    
    },

  created() {
    this.getCoupon();
  },
//  components: {
//     pagination
//   }
};


</script>


